@import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Text:wght@600&display=swap");

* {
    font-family: "Big Shoulders Text", cursive;
    padding: 0;
    margin: 0;
}
.Sidebar {
    height: 100vh;
    top: 0px;
}

.Cardbox {
    height: auto;
}

.counter {
    background-color: yellowgreen;
    color: black;
    text-align: center;
    position: absolute;
    height: 35px;
    width: 35px;
    top: -10px;
    right: -15px;
}

.counter2 {
    position: relative;
    bottom: -5px;
}

.heroName {
    position: absolute;
    display: block;
    color: white;
    background-color: orangered;
    transform-origin: bottom left;
    transform: rotate(-90deg);
    left: 28px;
    bottom: -8px;
    padding-right: 10px;
    padding-left: 5px;
}

.heroStats {
    position: absolute;
    bottom: 0;
    right: 0;
    padding-right: 10px;
}
